<template>
  <div class="course_details_container">
    <div class="course_details_top">
      <div v-on:click="backCourse">&lt;</div>
      <div v-show="showOne">课程详情</div>
      <div v-show="showThree" style="display:flex;justifyContent:space-around;">
        <div :class="selectIndex===index?'course_details_top_items_active':'course_details_top_items'" @click="selectTop(index)" v-for="(item,index) in topItem" :key="index">{{item}}</div>
      </div>
      <div>
        <img src="../assets/fenxiang.png" class="course_details_top_icon" alt />
      </div>
    </div>
    <div class="course_datails_main">
      <div class="course_details_content">
        <div class="course_details_title">
          <span>{{infoItems.title}}</span>
          <span style="float:right">
            <van-icon name="star" :color="show?'grey':'orange'" v-on:click="showIcon" style="margin-left:140px;" size="25"/>
            </span>
        </div>
        <div class="course_details_pay">{{infoItems.price | price}}</div>
        <div class="course_details_data">
          <span>共{{infoItems.total_periods}}课时</span>
          <span style="margin:0 10px;"> | </span>
          <span> {{infoItems.sales_num}}人已报名</span>
        </div>

      </div>

    <!-- 团队 -->
    <div class="course_details_team">
        <div style="font-size:16px;margin-left:5px;">教学团队</div>
        <div v-for="(item,index) in teacher" :key="index">
            <div>
                <img :src="item.avatar" alt="" class="course_details_team_photo">
            </div>
            <div style="margin-left:20px;font-size:14px;">{{item.teacher_name}}</div>
        </div>
    </div>

<!-- 课程介绍 -->
<div class="course_details_introduce">
  <div>课程介绍</div>
  <div v-for="(item,index) in introduce" :key="index">
    <div style="font-size:14px;color:grey;">{{item.periods_title}}</div>
  </div>
</div>
<!-- 课程介绍 -->
<div class="course_details_outline">
    <div style="font-size:16px;">课程大纲</div>
    <div class="course_details_course"  v-for="(item,index) in introduce" :key="index">
     <!-- <div class="course_details_course_top">
         <span class="course_details_course_top_span">回放</span>
         <span>第一讲第一课时</span>
     </div>
     <div class="course_details_course_bottom">
         <span style="margin:0 5px"></span>
         <span>03月16日 18:00 - 19:30</span>
     </div> -->
        <div style="font-size:14px;color:grey;">{{item.periods_title}}</div>
    </div>
</div>
<!-- 课程评论 -->
<div class="course_details_comment">
  <div class="course_details_comment_title" ref="commit">课程评论</div>

  <!-- 有评论 -->
    <div class="course_details_comment_content" v-for="(item,index) in comment" :key="index" v-show="commentItem">
        <div class="course_details_comment_content_left">
            <img :src="item.avatar" style="width:35px;marign-top:5px;" alt="">
        </div>
        <div class="course_details_comment_content_right">
            <div class="course_details_comment_content_right_top">
                <span style="font-size:16px;">{{item.nickname}}</span>
                <span style="color:grey;">{{item.created_at | time}}</span>
            </div>
            <div class="course_details_comment_content_right_bottom">
                <div>{{item.content}}</div>
            </div>
        </div>
    </div>

    <!-- 无评论 -->
    <div v-show="commentNone">
      <div><img src="../assets/comment.png" alt="" style="width:120px;margin-left:120px;"></div>
      <div style="width:100%;text-align:center;color:grey;">暂无评论</div>
    </div>
    <div style="width:100%;height:80px;"></div>
 </div>
 <div class="width:100%;height:50px"></div>
<div class="course_details_add">立即报名</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      infoItems:[],
      teacher:[],
      introduce:[],
      comment:[],
      topItem:["课程详情","课程大纲","课程评价"],
      commentItem:false,
      commentNone:false,
      show:true,
      showOne:true,
      showThree:false,
      selectIndex:0
    };
  },
  methods:{
      backCourse(){
        this.$router.back()
      },
      showIcon(){
        this.show=!this.show
        if(this.show==false){
       this.$http.post('/api/app/collect',{course_basis_id:this.$route.query.id,type:1}).then((res)=>{
          window.console.log(res.data)
          // this.code=res.data.code
        })
        }else{
          this.$http.put('/api/app/collect/cancel/231/1').then((res)=>{
            window.console.log(res.data)
          })
        }
      },
         handleScrollx() {
          // console.log('滚动高度', window.pageYOffset)
          // console.log('距离顶部高度', this.$refs.obtain.getBoundingClientRect().top)
          if(window.pageYOffset>0){
            this.showOne=false
            this.showThree=true
          }else{
            this.showOne=true
            this.showThree=false
          }
        },
        selectTop(index){
          this.selectIndex=index
          if(this.selectIndex==3){
            this.$refs.commit.scrollIntoView()
          }
        }
  },
  mounted() {
    this.$http.get('/api/app/courseInfo/basis_id='+this.$route.query.id).then((res)=>{
      // window.console.log(res.data.data)
      this.infoItems=res.data.data.info
      this.teacher=res.data.data.teachers
      // window.console.log(this.teachers)
    });
    this.$http.post('/api/app/courseChapter',{id:this.$route.query.id}).then((res)=>{
      // window.console.log(res.data.data)
      this.introduce=res.data.data
    });
    this.$http.post('/api/app/courseComment',{limit:10,page:1,id:this.$route.query.id}).then((res)=>{
      // window.console.log(res.data.data.list)
      this.comment=res.data.data.list
      if(this.comment.length==0){
        this.commentItem=false
        this.commentNone=true
      }else{
          this.commentItem=true
        this.commentNone=false
      }
    });
    　window.addEventListener('scroll', this.handleScrollx, true)


  },
  filters:{
    price(val){
      if(val==0){
        return '免费'
      }else{
        return `￥${(val/100).toFixed(2)}`
      }
    },
    time(value){
       let date = new Date(parseInt(value) * 1000);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return y+'-'+m + "-" + d + " " + h + ":" + minute

    }
  }
};
</script>

<style>
::-webkit-scrollbar{
    display: none;
}
.course_details_container {
  width: 100vw;
  height: 100vh;
}
.course_details_top {
  width: 96%;
  height: 50px;
  background: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2%;
  position:fixed;
  top:0;
}
.course_details_top_items{
 margin:0 20px;
 color:grey;
 font-size:16px;
}
.course_details_top_items_active{
 margin:0 20px;
 color:black;
 font-size:16px;
}
.course_details_top_icon {
  width: 20px;
}
.course_details_content {
  width: 94%;
  height: 130px;
  background: white;
  font-size: 16px;
  padding:0 2%;
}
.course_details_title {
  font-size: 18px;
  margin-top: 10px;
}
.course_details_pay {
  width: 100%;
  height: 35px;
  display: flex;
  align-items: center;
  color: orange;
}
.course_details_data {
  width: 100%;
  height: 35px;
  display: flex;
  align-items: center;
  color: grey;
}
.course_datails_time {
  width: 100%;
  height: 35px;
  display: flex;
  align-items: center;
  color: grey;
}
.course_datails_main {
margin-top:50px;
  width: 100%;
  /* height: 800px; */
  background: rgb(240, 242, 245);
}
.course_details_team{
    width:100%;
    height: 120px;
    margin-top:15px;
   background:white;;
}
.course_details_team_photo{
    width:40px;
    margin-top:10px;;
    margin-left:20px;
    border-radius:100%;
}
.course_details_introduce{
  width:100%;
  height: 60px;
  margin-top:15px;
  background: white;
  font-size:16px;
  padding-left:5px;
  padding-top:10px;
}
.course_details_outline{
    width:96%;
    /* height: 400px; */
    background:white;
    margin-top:15px;
    padding:0 2%;;
}
.course_details_course{
    width:100%;
    height: 55px;
    /* background:pink; */
    font-size:14px;
    margin-top:5px;
}
.course_details_course_top_span{
    background:orange;
    color:white;
    font-size:14px;
    margin-right: 5px;;
}
.course_details_course_bottom{
   margin-top:5px;
   color:grey;
}
.course_details_comment{
    width:96%;
    /* height: 300px; */
    padding:0 2%;
    margin-top:15px;
    background:white;
    font-size:14px;;
}
.course_details_comment_title{
 font-size:16px;
}
.course_details_comment_content{
    width:100%;
